* {
    margin: 0;
    padding: 0;
    -moz-box-sizing: border-box; /* Firefox */
    -ms-box-sizing: border-box; /* Internet Explorer */
    -webkit-box-sizing: border-box; /* Google Chrome & Safari */
    box-sizing: border-box;
    -moz-user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
    user-select: none;
}
 
html {
    background-color: #000000;
}
 
#h1 {
    color: #f2f2f2;
    text-align: center; 
    font-size: 90px;
    margin-top: 0px; 
    font-weight: bolder; 
    letter-spacing: 3px;
    word-spacing: 7px;
    text-shadow: 3px 3px 2px rgba(255,255,255,0.5);
}
 
#mainApp {
    background-color: #196666;
    position: relative;
    min-height: 500px;
    width: 600px;
    margin: 30px auto;
    border-radius: 40px;
    box-shadow: 5px 7px 5px 5px rgba(0,26,51,0.5);
    padding: 2px 50px 50px 50px;
}
 
#input {
    position: relative;
    width: 500px;
    height: 50px;
    background-color: #1a1a1a;
    color: black;
    padding: 20px;
    margin: 20px auto;
    font-size: 25px;
    color: white;
    border-radius: 10px;
    border: 0px;
}
 
#input:focus {
    outline: none;
}
 
#input::placeholder {
    color: rgba(13, 13, 13, 0.5);
}
 
.listDiv {
    background-color: #8080ff;
    min-width: 450px;
    min-height: 50px;
    border-radius: 5px;
    border: 0px;
    color: #080846;
    font-size: 25px;
    font-weight: bolder;
    margin: 10px auto;
}
 
.listDiv p {
    position: relative;
    padding: 10px;
}
 
.listDiv p button {
    position: absolute;
    right: 5px;
    width: 30px;
    height: 30px;
    font-weight: bold;
    font-size: 20px;
    background-color: #e6e6e6;
    text-shadow: 2px 2px 2px rgba(94,119,115,0.82);
    border: 1px solid grey;
    box-shadow: 2px 2px 1px 0px rgba(119,116,115,1);
}
